import { createRouter, createWebHistory } from 'vue-router'
import Top from '@/views/Top.vue'
import About from '@/views/About.vue'
import NotDesign from '@/pages/NotDesign.vue'
import AlgorithmSketch from '@/pages/AlgorithmSketch.vue'
import Cosmos from '@/pages/Cosmos.vue'
import Holographic from '@/pages/Holographic.vue'
import Kakitori from '@/pages/Kakitori.vue'
import Mole from '@/pages/Mole.vue'
import Pingpong from '@/pages/Pingpong.vue'
import PlantCamera from '@/pages/PlantCamera.vue'
import Shadow from '@/pages/Shadow.vue'
import SeaSurfaceTemperature from '@/pages/SeaSurfaceTemperature.vue'
import ShaderSchool from '@/pages/ShaderSchool.vue'
import ShaderSketch from '@/pages/ShaderSketch.vue'
import AjinomotoOlyPara from '@/pages/AjinomotoOlyPara.vue'
import TakiTv from '@/pages/TakiTv.vue'
import Speedskating from '@/pages/Speedskating.vue'


const routes = [
  { path: '/', name: 'Top', component: Top },
  { path: '/about', name: 'About', component: About },
  { path: '/work/not_design', name: 'NotDesign', component: NotDesign },
  { path: '/work/algorithm_sketch', name: 'AlgorithmSketch', component: AlgorithmSketch },
  { path: '/work/cosmos', name: 'Cosmos', component: Cosmos },
  { path: '/work/holographic', name: 'Holographic', component: Holographic },
  { path: '/work/kakitori', name: 'Kakitori', component: Kakitori },
  { path: '/work/mole', name: 'Mole', component: Mole },
  { path: '/work/pingpong', name: 'Pingpong', component: Pingpong },
  { path: '/work/plant_camera', name: 'PlantCamera', component: PlantCamera },
  { path: '/work/shadow', name: 'Shadow', component: Shadow },
  { path: '/work/sea_surface_temperature', name: 'SeaSurfaceTemperature', component: SeaSurfaceTemperature },
  { path: '/work/shader_school', name: 'ShaderSchool', component: ShaderSchool },
  { path: '/work/shader_sketch', name: 'ShaderSketch', component: ShaderSketch },
  { path: '/work/ajinomoto_olypara', name: 'AjinomotoOlyPara', component: AjinomotoOlyPara },
  { path: '/work/takitv', name: 'TakiTv', component: TakiTv },
  { path: '/work/speedskating', name: 'Speedskating', component: Speedskating },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { top: 0 }
    }
  }
})

export default router
